.earth {
  position: absolute;
  bottom: -55px;
  left: 50%;
  width: calc(100% + 16px);
  height: 296px;
  background: -webkit-gradient(linear, left top, left bottom, from(#926374), to(#2f2546));
  background: -webkit-linear-gradient(top, #926374, #2f2546);
  background: -o-linear-gradient(top, #926374, #2f2546);
  background: linear-gradient(to bottom, #926374, #2f2546);
  overflow: hidden;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  -webkit-perspective: 100px;
          perspective: 100px;
  z-index: 10;
  -webkit-box-shadow: inset 0 10px 30px -10px #4B3757;
          box-shadow: inset 0 10px 30px -10px #4B3757; }

.earth .road {
  position: absolute;
  width: 100px;
  height: 50%;
  background: #614761;
  top: 50%;
  left: 30%;
  -webkit-transform: translate(-50%, -50%) skewX(-20deg) rotateX(15deg);
          transform: translate(-50%, -50%) skewX(-20deg) rotateX(15deg);
  -webkit-perspective: 100px;
          perspective: 100px;
  border-left: 10px solid #855d76; }

.earth .road::after, .earth .road::before {
  content: '';
  position: absolute;
  width: 100px;
  height: 50%;
  background: #614761; }

.earth .road::after {
  top: 108px;
  left: -23px;
  width: 130px;
  -webkit-transform: skewX(30deg) rotateX(85deg);
          transform: skewX(30deg) rotateX(85deg);
  border-left: 10px solid #855d76; }

.earth .road::before {
  bottom: 77%;
  height: 200%;
  left: -156px;
  width: 55px;
  -webkit-transform: skewX(50deg) rotateX(15deg);
          transform: skewX(50deg) rotateX(15deg);
  border-right: 10px solid #4f3957; }



