.cactus {
  position: absolute;
  background: #A06C7A;
  left: 10%;
  bottom: 240px;
  height: 25px;
  width: 10px;
  border-radius: 10px 10px 0 0; }

.cactus::after, .cactus::before {
  content: '';
  position: absolute;
  border-bottom: 5px solid #A06C7A;
  border-left: 5px solid transparent;
  border-right: 5px solid transparent;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  left: -10px;
  top: -2px;
  -webkit-transform: rotateZ(45deg);
      -ms-transform: rotate(45deg);
          transform: rotateZ(45deg); }

.cactus::before {
  left: 0;
  top: 5px;
  -webkit-transform: rotateZ(-45deg);
      -ms-transform: rotate(-45deg);
          transform: rotateZ(-45deg); }

.cactus.model-b::after {
  top: 2px; }

.cactus.model-b::before {
  left: 0;
  top: 0;
  -webkit-transform: rotateZ(-45deg);
      -ms-transform: rotate(-45deg);
          transform: rotateZ(-45deg); }

.cactus.model-c::after {
  top: 0; }

.cactus.model-c::before {
  left: 0;
  top: 6px;
  -webkit-transform: rotateZ(-45deg);
      -ms-transform: rotate(-45deg);
          transform: rotateZ(-45deg); }



