.desert {
  background: -webkit-gradient(linear, left top, left bottom, from(#fbac86), to(#FEF5E0));
  background: -webkit-linear-gradient(top, #fbac86, #FEF5E0);
  background: -o-linear-gradient(top, #fbac86, #FEF5E0);
  background: linear-gradient(to bottom, #fbac86, #FEF5E0);
  z-index: -999;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%; }



